.svg {
  width: 40px;
  height: 31px;
}
.svg_text {
  font-size: 20px;
  fill: rgb(85, 85, 85);
}
.svg_text_load {
  font-size: 12px;
  transition: font-size .5s;
  fill: rgb(85, 85, 85);
  transform-origin: 50% 50%;
  animation: ratateText 1s infinite linear;
}
@keyframes ratateText {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}